.sub-header-container {
  display: flex;
  width: '100%';
  margin-top: 30px;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 5px;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  line-height: 20px;
}
.sub-header-text {
  color: theme-color('primary-blue');
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.4px;
  font-family: 'Inter Bold Heavy';
}
.sub-header-description {
  color: theme-color('slate-gray');
  text-align: center;
  font-family: 'Inter Light';
  letter-spacing: 0.1px;
  line-height: 20px;
  font-size: 14px;
}
