@import '../../../../assets/theme/colors';
@import '../../../../assets/theme/fonts.scss';
@import '~bootstrap/scss/functions';

.approval-card-parent-container {
  position: absolute;
  max-width: 375px;
  margin-bottom: 0px;
  margin-top: 0px;
  padding-bottom: 0px;
  width: 100%;
}
.title {
  color: theme-color('primary-blue');
  line-height: 24px;
  font-family: 'Inter H1 Bold';
  font-size: 20px;
  letter-spacing: 0.4px;
  margin: 0px;
}
.sub-title {
  color: theme-color('primary-blue');
  font-size: 16px;
  font-weight: 400;
  line-height: 21px;
  letter-spacing: 0.4px;
  font-family: 'Inter Regular';
  text-transform: capitalize;
}
b {
  font-family: 'Inter Semi Bold';
}
.button-cp {
  background-color: theme-color('primary-green') !important;
  color: theme-color('primary-blue') !important;
  border-width: 0px !important;
}
.button-cp:hover {
  border-width: 0px;
  background-color: #9ef500 !important;
  color: theme-color('primary-blue') !important;
}

.button-wf {
  background-color: theme-color('secondary-blue');
  color: theme-color('primary-blue') !important;
  border-width: 0px !important;
}
.button-get-start-item-approval {
  background-color: theme-color('primary-blue');
  border-width: 0px !important;
}
.button-wf:hover {
  color: theme-color('white') !important;
  background-color: theme-color('secondary-blue-hover') !important;
}
.button-return-uncleaned {
  background-color: theme-color('secondary-red') !important;
  color: theme-color('red') !important;
  border-width: 0px !important;
}
.button-return-uncleaned:hover {
  background-color: #ffe2eb !important;
  color: theme-color('red') !important;
}
.special-instruction {
  color: theme-color('slate-gray');
  font-size: 14px;
  margin-bottom: 10px;
  padding-top: 20px;
  line-height: 20px;
  letter-spacing: 0.1px;
  font-family: 'Inter Light';
}
