@import '../../../../assets/theme/colors';
@import '../../../../assets/theme/fonts.scss';
@import '~bootstrap/scss/functions';

.body {
  font-size: 14px;
  font-weight: 300;
  color: theme-color('primary-blue');
  width: 100%;
  height: 100%;
  font-family: 'Inter Light';
  letter-spacing: 0.1px;
}
.container {
  flex: 1;
  padding: 0px;
  max-width: 375px;
}

// ITEM CONTAINER

.item-container {
  height: 52px;
  display: flex;
  align-items: center;
  flex-shrink: 0;
}
.item-container-parent {
  align-items: center;
  flex-shrink: 0;
  border-radius: 6px;
  border: 1px solid #f2f3f8;
  background: #fff;
  padding-left: 15px;
  padding-right: 15px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.item-text {
  color: theme-color('primary-blue');
  flex: 1;
  letter-spacing: 0.1px;
  padding-left: 10px;
  font-size: 14px;
  font-family: 'Inter Regular';
}
.item-heading-description {
  color: theme-color('primary-blue');
  font-size: 14px;
  font-family: 'Inter Light';
  margin-bottom: 16px;
  line-height: 20px;
}

.item-sub-section {
  display: flex;
  align-items: flex-start;
  font-family: 'Inter Regular';
  justify-content: space-between;
  font-size: 14px;
  padding-top: 2px;
}
.cirlce-container {
  width: 40px;
}
.bullets-points-container {
  display: flex;
  border-width: 0px;
  padding-top: 16px;
  padding-bottom: 16px;
  margin: 0px !important;
  margin-bottom: 20px;
}
.bullets-points-text {
  margin-left: 4px;
  padding: 0px;
  margin: 0px;
  flex: 1;
}
.seperator {
  height: 0.5px !important;
  width: 100%;
  background: theme-color('light-gray');
}

.bottomSpacing {
  height: 70px;
}

.continueButton {
  font-family: 'Inter Medium';
  background-color: theme-color('primary-blue');
}
.skipButton {
  font-family: 'Inter Medium';
  background-color: white;
  color: theme-color('primary-blue');
  border-color: theme-color('primary-blue');
}

@media (max-width: 300px) {
  .sub-header-text {
    font-size: 14px;
  }
  .sub-header-description {
    font-size: 12px;
  }
  .item-text {
    font-size: 12px;
  }
  .item-heading-description {
    font-size: 12px;
    margin-top: 3px;
  }
  .item-sub-section {
    font-size: 12px;
  }
}
