@import '../../../../assets/theme/colors';
@import '~bootstrap/scss/functions';

body {
  background-color: theme-color('grey');
  height: 100%;
}

.container {
  max-width: 375px;
  height: 100vh;
  flex: 1;
}
.ribbon-label {
  position: absolute;
  bottom: 0.1875rem;
  left: -0.25rem;
}
.reports-image {
  border-bottom-left-radius: calc(0.25rem - 1px);
  border-bottom-right-radius: calc(0.25rem - 1px);
}

@media (max-width: 320px) {
  .ribbon-label {
    width: 100%;
    left: -0.188rem;
    bottom: -3px;
  }
  .single-label {
    width: initial;
    bottom: 0px;
    left: -0.282rem;
  }
}

.stain-message {
  font-size: 0.75rem;
}
